.container {
  max-width: $ds;
  min-width: $sm;
  margin: 0 auto;
  padding: 0 10px;

  @media (min-width: $tb) {
    padding: 0 20px;
  }
}

.containerBike {
  max-width: $ds + 186px;
  min-width: $sm;
  margin: 0 auto;
  padding: 0 10px;

  @media (min-width: $tb) {
    padding: 0 20px;
  }
}


.containerMain {
  max-width: $ds + 221px;
  min-width: $sm;
  margin: 0 auto;
  padding: 0 10px;

  @media (min-width: $tb) {
    padding: 0 20px;
  }
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"] .visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.Mainpage-bike {
  font-size: 10px;
  line-height: 1.15;

  &__body {
    margin: 0;
    font-family: "Open Sans", "Arial", sans-serif;
  }
}

.link {
  text-decoration: none;
  cursor: pointer;

  &:hover {
    opacity: 0.4;
  }
}

.none {
  display: none;
}
