.f-basicLight-Small,
.f-basicLight-Middle,
.f-basicLight-Big,
.f-basicLightT {
  font-weight: 300;
  line-height: 1.52;
}

.f-basicLight-Small {
  font-size: 1.8rem;
  color: $white;
  line-height: 1.72;

  @media (min-width: $tb) {
    line-height: 1.72;
  }

  @media (min-width: $ds) {
    font-size: 2rem;
    line-height: 1.52;
  }
}

.f-basicLight-Middle {
  font-size: 2rem;
}

.f-basicLight-Big {
  font-size: 2rem;
}

.f-basicLight-menu {
  font-size: 2rem;
  font-weight: 400;
  color: $basicGray1;

  @media (min-width: $ds) {
    font-weight: 300;
    font-size: 1.6rem;
    color: $white;
  }
}

.f-basicLightT {
  font-size: 1.6rem;
  color: $white;
}

///regular
.f-basicRegular,
.f-basicRegular-Small {
  font-weight: 400;
  line-height: 1.87;
}

.f-basicRegular-Small {
  font-size: 1.5rem;
  color: $basicGray2;
}

.f-basicRegular-Small2 {
  font-size: 1.7rem;
  color: $basicGray6;
  line-height: 1.31;

  @media (min-width: $ds) {
    color: $basicGray3;
  }
}

.f-basicRegular-Small3 {
  font-size: 1.6rem;
  color: $basicGray3;
}

.f-basicRegular {
  font-size: 1.8rem;
  color: $black;

  @media (min-width: $tb) {
    font-size: 2.2rem;
    line-height: 1.34;
  }
}

///semibold
.f-basicSemiBold,
.f-basicSemiBold-Small,
.f-basicSemiBold-Small2,
.f-basicSemiBold-Middle,
.f-basicSemiBold-Middle2,
.f-basicSemiBold-Middle3 {
  font-weight: 600;
  color: $black;
  line-height: 1.21;
}

.f-basicSemiBold {
  font-size: 1.8rem;
  line-height: 1.58;

  @media (min-width: $ds) {
    font-size: 1.7rem;
    color: $white;
  }
}

.f-basicSemiBold-Small {
  font-size: 1.5rem;
}

.f-basicSemiBold-Small2 {
  font-size: 2.2rem;
  line-height: 1.34;
  letter-spacing: -0.5px;
}

.f-basicSemiBold-Middle {
  font-size: 2.4rem;
  line-height: 1.41;
  color: $basicGray5;

  @media (min-width: $ds) {
    font-size: 3rem;
    line-height: 1.3;
  }
}

.f-basicSemiBold-Middle2 {
  font-size: 2.4rem;
  line-height: 1.3;
  color: $white;

  @media (min-width: $ds) {
    font-size: 3rem;
  }
}


.f-basicSemiBold-Middle3 {
  font-size: 1.8rem;
  line-height: 1.3;
  color: $basicGray5;

  @media (min-width: $ds) {
    font-size: 3rem;
  }
}


////////bold

.f-basicBold,
.f-basicBold-Decoration {
  font-weight: 700;
}

.f-basicBold {
  font-size: 3rem;
  color: $white;
  line-height: 1.42;

  @media (min-width: $ds) {
    font-size: 4.6rem;
    line-height: normal;
  }
}

.f-basicBold-Decoration {
  font-size: 2.8rem;
  color: $basicdecor;
  line-height: 0.38;
}
